import * as React from 'react';
import { Controller, RenderNodeProps } from '@ali/4ever-cangjie';
import { tagFactory } from '@ali/4ever-factory';
import { BiPluginConfig } from '../../types';
import { ImTag, getMeasureWidth } from './components/ImTag';

const createBiImTagPlugin = tagFactory((bizConfig: BiPluginConfig) => {
  return {
    tagType: 'im',
    renderNode: (props: RenderNodeProps, controller: Controller) => {
      return (
        <ImTag
          props={props}
          controller={controller}
          imTagConfig={bizConfig.imTag}
        />
      );
    },
    models: {
      klass: 'inline',
      type: 'tag',
    },
  };
}, () => ({
  key: 'imTag'
}));

export { getMeasureWidth };

export default createBiImTagPlugin;
